.Skills-Section {
  /* height: 100vh; */
  background-color: white;
  overflow: hidden;
}

.Skills-Content {
  padding-top: 5em;
}

.Skill-ShowCase {
  width: 95%;
  margin: auto;
  /* border: 2px solid red; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.Skill-Box {
  min-width: 180px;
  max-width: 180px;
  padding: 1em;
  /* border: 2px solid red; */
  cursor: pointer;
  position: relative;
  overflow: hidden;
  margin: 10px;
}

.Skill-Image {
  width: 100%;
}

.Skill-Name {
  margin-top: 8px;
  text-align: center;
}

.Skill-Details {
  position: absolute;
  top: 0;
  left: 0;
  background-color: white;
  color: black;
  width: 100%;
  height: 100%;
  /* opacity: 0.8; */
  transform: translateX(-100%);
}

.Skill-Box:hover .Skill-Details {
  transform: translateX(0);
  transition: all 0.5s;
}
