.scroll-down{
    position: absolute;
    bottom: 35px;
    /* border: 2px solid red; */
    /* transform: translateY(-80px) rotate(45deg); */
}

.scroll-down span{
    width: 30px;
    height: 30px;
    display: block;
    border-bottom: 2px solid white;
    border-right: 2px solid white;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-animation: scroll 2s ease-in-out infinite;
            animation: scroll 2s ease-in-out infinite;
}

.scroll-down span:nth-child(1)
{
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
}

.scroll-down span:nth-child(2)
{
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
}

@-webkit-keyframes scroll{

    0%{
        opacity: 0;
        -webkit-transform: translateY(0) rotate(45deg);
                transform: translateY(0) rotate(45deg);
        
    }

    25%{
        opacity: 1;
    }

    50%,100%{
        opacity: 0;
        -webkit-transform: translateY(10px) rotate(45deg);
                transform: translateY(10px) rotate(45deg);
    }
}

@keyframes scroll{

    0%{
        opacity: 0;
        -webkit-transform: translateY(0) rotate(45deg);
                transform: translateY(0) rotate(45deg);
        
    }

    25%{
        opacity: 1;
    }

    50%,100%{
        opacity: 0;
        -webkit-transform: translateY(10px) rotate(45deg);
                transform: translateY(10px) rotate(45deg);
    }
}


/*Floating Icons*/
.Li-Circle{
    border: 2px solid white;
    padding: 1em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    cursor: pointer;
    background-color: white;
    border-radius: 50%;
}

.bg-red{
    background-color: rgb(252, 122, 122);
}

.email-li{
    position: absolute;
    top: 20%;
    right: 50%;
    -webkit-animation: floating 3.3s linear infinite;
            animation: floating 3.3s linear infinite;
}

.Linkedin-li{
    position: absolute;
    top: 75%;
    right: 85%;
    /* color: blue; */
    -webkit-animation: floating 3s linear 1s infinite;
            animation: floating 3s linear 1s infinite;
}

.gitHub-li{
    position: absolute;
    top: 70%;
    right: 10%;
    /* color: blue; */
    -webkit-animation: floating 3s linear infinite;
            animation: floating 3s linear infinite;
}

@-webkit-keyframes floating {

    0%, 100%{
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }

    50%{
        -webkit-transform: translateY(15px);
                transform: translateY(15px);
    }
    
}

@keyframes floating {

    0%, 100%{
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }

    50%{
        -webkit-transform: translateY(15px);
                transform: translateY(15px);
    }
    
}



