.Footer-Section{
    margin-top: 10vmin;
    background-color: #000;
    /* height: 50vh; */
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 1.5em;
}

.FooterEmail{
    font-weight: bold;
    font-size: 4rem;
    text-decoration: none;
    color: white;
    text-align: center;
}

.FooterEmail:hover{
    color: white;
    transform: scale(0.7);
    transition: all 0.3s;
}

.Footer-Writes{
    font-weight: bold;
    font-size: 1.5rem;
}

.Links{
    text-decoration: none;
    color: white;
}

.Links:hover{
    color: white;
    transform: scale(1.2);
    transition: all 0.3s;
}

@media(max-width: 1150px)
{
    .FooterEmail{
        font-weight: bold;
        font-size: 2rem;
    }
    
    .Footer-Writes{
        font-weight: bold;
        font-size: 1rem;
    }
}

@media(max-width: 565px)
{
    .FooterEmail{
        font-weight: bold;
        font-size: 1.2rem;
    }
    
    .Footer-Writes{
        font-weight: bold;
        font-size: 0.9rem;
    }
}