.Home-Section{
    height: 100vh;
    position: relative;
    background-color: black;
    /* border: 2px solid white; */
}

.Content{
    width: 100%;
    height: 100%;
    /* border: 2px solid red; */
}

.Hero-Content{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 95%;
    margin: 0 auto;
}

.My-Name {
    font-family: Montserrat, Helvetica, Arial, sans-serif;
    font-size: 6rem;
    font-style: normal;
    font-weight: bold;
    /* letter-spacing: -6px; */
    /* line-height: 130px; */
    color: #FFFFFF;
}

.Side-Text {
    font-family: Montserrat, Helvetica, Arial, sans-serif;
    font-size: 1.6rem;
    /* line-height: 35px; */
    color: #fff;
}

.intro-image{
    width: 50%;
}

.scroll-down{
    position: absolute;
    bottom: 35px;
    /* border: 2px solid red; */
    /* transform: translateY(-80px) rotate(45deg); */
}

.scroll-down span{
    width: 30px;
    height: 30px;
    display: block;
    border-bottom: 2px solid white;
    border-right: 2px solid white;
    transform: rotate(45deg);
    animation: scroll 2s ease-in-out infinite;
}

.scroll-down span:nth-child(1)
{
    animation-delay: 0.8s;
}

.scroll-down span:nth-child(2)
{
    animation-delay: 1.2s;
}

@keyframes scroll{

    0%{
        opacity: 0;
        transform: translateY(0) rotate(45deg);
        
    }

    25%{
        opacity: 1;
    }

    50%,100%{
        opacity: 0;
        transform: translateY(10px) rotate(45deg);
    }
}


/*Floating Icons*/
.Li-Circle{
    border: 2px solid white;
    padding: 1em;
    width: fit-content;
    cursor: pointer;
    background-color: white;
    border-radius: 50%;
}

.bg-red{
    background-color: rgb(252, 122, 122);
}

.email-li{
    position: absolute;
    top: 20%;
    right: 50%;
    animation: floating 3.3s linear infinite;
}

.Linkedin-li{
    position: absolute;
    top: 75%;
    right: 85%;
    /* color: blue; */
    animation: floating 3s linear 1s infinite;
}

.gitHub-li{
    position: absolute;
    top: 70%;
    right: 10%;
    /* color: blue; */
    animation: floating 3s linear infinite;
}

@keyframes floating {

    0%, 100%{
        transform: translateY(0px);
    }

    50%{
        transform: translateY(15px);
    }
    
}



