.Works-Section{
    /* height: 110vh; */
    background: white;
    padding-top: 7em;
    /* padding-bottom: 7.2em; */
    overflow: hidden;
}

.Works-content{
    /* border: 2px solid red; */
    width: 95%;
    /* height: 90%; */
    margin: 0 auto;
}

.Projects-ShowCase{
    margin-top: 7vmin;
    /* border: 2px solid black; */
    display: grid;
    grid-template-columns: 2fr 2fr;
    grid-gap: 20px;
    justify-items: center;
}

.Project-Box{
    cursor: pointer;
}

.img-text-wrapper{
    position: relative;
    /* border: 2px solid red; */
}

.Project-image{
    width: 550px;
}

.Project-Badge{
    position: absolute;
    top: 10px;
    right: 15px;
    background-color: black;
    color: white;
    font-size: 0.8rem;
    padding: 0.6em 1.5em;
    border-radius: 20px;
    font-weight: lighter;
}

.Project-Name{
    /* text-align: center; */
    margin-top: 0.3em;
    font-weight: bold;
}

/* .Project-Box:hover{
    transform: scale(0.8);
    transition: all 0.8s;
} */

.hoverEfeect{
    transform: scale(0.8);
    transition: all 0.8s;
}

.No-Link{
    text-decoration: none;
    color: black;
}

.No-Link:hover{
    color: black;
}

@media (max-width: 750px)
{
    .Projects-ShowCase{
        /* border: 2px solid black; */
        display: block;
    }

    .Project-image{
        width: unset;
    }

    .Project-Box{
        margin-bottom: 6vmin;
    }

    .Project-Badge{
        position: absolute;
        top: 10px;
        right: 15px;
        background-color: black;
        color: white;
        font-size: 0.6rem;
        padding: 0.6em 1.5em;
        border-radius: 20px;
        font-weight: lighter;
    }
}





/*DESC COMMONS*/
.Section{
    color: white;
    padding: 0 4em;
}

.Desc-Heading{
    font-weight: bold;
    font-size: 3rem;
}

.Desc-Writings{
    /* font-weight: bold; */
    font-size: 1.6rem;
}

.NAV{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 1.2rem;
    padding-top: 1em;
    /* cursor: pointer; */
}

.Desc-Container{
    margin-top: 5vmin;
}

.Video{
    width: 80%;
    margin: 0 auto;
}

.BACK{
    text-align: center;
    text-decoration: none;
    color: white;
}

.BACK:hover{
    color: white;
    transform: scale(1.5);
    transition: all 0.5s;
}

.Project-Links{
    font-size: 1.6rem;
    text-decoration: none;
    color: white;
    /* font-weight: bold; */
}

.Project-Links:hover{
    transform: scale(0.8);
    color: white;
    transition: all 0.5s;
}






/*Works Summary Component*/
.Summary-section{
    margin-top: 13vmin;
    height: 80vh;
    width: 100%;
}

.Summary-Container{
    width: 95%;
    margin: 0 auto;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    /* border: 1px solid black; */
    height: 80%;
}

.button-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border: 2px solid black; */
    padding: 0.5em;
    width: 30%;
    height: 90%;
}

.summary-btn{
    background-color: #FFC2C7;
    padding: 0.5em;
    border: none;
    width: 90%;
    margin-bottom: 2vmin;
}

.summary-act{
    background-color: #F41F4E;
    color: white;
    transition: all 0.3s;
    padding: 0.5em;
    border: none;
    margin-bottom: 2vmin;
    width: 90%;
}

.summary-btn:hover{
    background-color: #F41F4E;
    color: white;
}

.summary-items-container{
    border-left: 1px solid black;
    padding-left: 2em;
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    width: 60%;
}

@media(max-width: 610px)
{
    .Summary-section{
        margin-top: 15vmin;
        height: unset;
    }
    
    .Summary-Container{
        width: 95%;
        margin: 0 auto;
        display: unset;
    }

    .button-container{
        display: unset;
        flex-direction: unset;
        justify-content: unset;
        align-items: unset;
        width: unset;
        height: unset;
        display: grid;
        grid-template-columns: 2fr 2fr;
        /* border: 2px solid black; */
        padding-left: 2em;
    }

    .media-heading{
        font-size: 2.1rem;
    }

    .summary-items-container{
        border-left: none;
        padding-left: unset;
        height: unset;
        /* border: 2px solid black; */
        text-align: center;
        padding: 0 1em;
        display: unset;
        justify-content: unset;
        align-items: unset;
        flex-direction: unset;
    }
}

